import { RotatingLines } from 'react-loader-spinner';

import { LoaderBox } from './Loader.styled';

export const Loader = ({ visible }) => {
  return (
    <LoaderBox>
      <RotatingLines
        strokeColor="grey"
        strokeWidth="5"
        animationDuration="0.75"
        width="96"
        visible={visible}
      />
    </LoaderBox>
  );
};